 .img-map {
   width: 100%;
   height: 100%;  
 }
 .dots {
   position: absolute;
   top: 0; right: 0; bottom: 0; left: 0;
 }
 .map-dot {
   position: absolute;
   width: 9px; height: 9px;
   border-radius: 50%;
   background-color: orange;
   border: 1px solid white;
   text-decoration: none;
 }
 .map-dot .text {
   display: none;
 }
 .map-dot:hover .text {
   display: block;
   position:absolute;
   margin-left: 40px;
   margin-top: -30px;
   background-color: black;
   opacity: .7;
   color: white;
   padding: 1.5rem;
   width: 160px;
 }
 .map-dot:hover .text:before {
   content: '';
   border-bottom: 12px solid transparent;
   border-right: 14px solid black;
   border-top: 12px solid transparent;
   display: block;
   margin-left: -14px;
   margin-top: 22px;
   position: absolute;
   top: 0;
   left: 0;
 }
 .title {
   display: block;
     color: orange;
     text-transform: uppercase;
     margin-bottom: 1rem;
 }
 
 .map-dot::before, .map-dot::after {
   content: "";
   position: absolute;
   top: -2rem; right: -2rem; bottom: -2rem;     left: -2rem;
   border: 1px solid red;
   border-radius: inherit;
   opacity: 1;
   transform: scale(0);
   -webkit-animation: circles 3s infinite;
           animation: circles 3s infinite;
 }
 
 .map-dot::after {
   -webkit-animation-delay: .3s;
           animation-delay: .3s;
 }
 
 @media(min-width: 768px) {
   
   @-webkit-keyframes circles {
     to {
       opacity: 0;
       transform: scale(1);
     }
   }
   
   @keyframes circles {
     to {
       opacity: 0;
       transform: scale(1);
     }
   }
 }

